import React from "react";
import '../css/style.scss';
import { useEffect, useRef } from "react";
import { OneByOne } from "../js/oson";
import { Reveal } from '../js/oson';
import { motion } from "framer-motion";

const Project1 = () => {

    const headerBg = useRef();
    const headerTitle = useRef();
    const headerText = useRef();
    const project2 = useRef([]);
    const project = (el) => {
        if(el && !project2.current.includes(el)){
            project2.current.push(el);
        };
    };

    const s1Box2 = useRef([]);
    const s1Box = (el) => {
        if(el && !s1Box2.current.includes(el)){
            s1Box2.current.push(el);
        };
    };


    useEffect(() => {
        window.scrollTo(0, 0);

        if(document.body.hasAttribute('linked')){
            setTimeout(() => {
                headerBg.current.classList.add('active');
                OneByOne(titleSpan, 'active', 0.05, 1.5);
                setTimeout(() => {
                    headerText.current.classList.add('active');
                }, 1000);
            }, 2700);
        }else{
            setTimeout(() => {
                headerBg.current.classList.add('active');
                OneByOne(titleSpan, 'active', 0.05, 1.5);
                setTimeout(() => {
                    headerText.current.classList.add('active');
                }, 1000);
            }, 800);
        };

        
        headerTitle.current.innerHTML = headerTitle.current.innerText
        .split("")
        .map((char, i) => `<span style="--i:${i + 1};" class="headerSpan">${char}</span>`)
        .join("");
        
        const titleSpan = document.querySelectorAll('.headerSpan');


        setTimeout(() => {
            s1Box2.current.forEach(e => {
              e.classList.add('active');
            })
        }, 1000);



        new Reveal({
            element: project2.current,
            className: 'active',
            distance: 30,
        });

    });

    return ( 
        <motion.div className="Pro"
            initial={{ opacity: 0, transition: {duration: 0} }}
            animate={{ opacity: 1,transition: {duration: 1} }}
            exit={{ opacity: 0, transition: {duration: 0} }}
        >

            <header className="header">
                <div className="header__container">
                    <div className="header__bg" ref={headerBg}>
                        <img src={require('../img/Projects/card4.jpg')}/>
                    </div>
                    <div className="header__info">
                        <h1 className="header__title" ref={headerTitle}>Project-1</h1>
                        <div className="header__text-wrapper">
                            <p className="header__text" ref={headerText}>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            </p>
                        </div>
                    </div>
                </div>
            </header>

            <div className="s1__box">
                <div className="s1__box-content" ref={s1Box}>
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                </div>
                <div className="s1__box-content" ref={s1Box}>
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                </div>
            </div>
            
            <section className="s1">
                <div className="s1__container">
                    <div className="s1__content">

                        <div className="project-layout-1" ref={project}>
                            <div className="image-left">
                                <p className="text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                                <img src={require('../img/Projects/card4.jpg')}/>
                            </div>
                            <div className="image-right">
                                <img src={require('../img/Projects/card3.jpeg')}/>
                            </div>
                        </div>

                        <div className="project-layout-1 lay2" ref={project}>
                            <div className="image-left">
                            <p className="text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                        <img src={require('../img/Projects/card1.jpeg')}/>
                        </div>
                            <div className="image-right">
                                <img src={require('../img/Projects/card2.jpg')}/>
                            </div>
                        </div>

                        <div className="project-layout-1" ref={project}>
                            <div className="image-left">
                                <p className="text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                                <img src={require('../img/Projects/card4.jpg')}/>
                            </div>
                            <div className="image-right">
                                <img src={require('../img/Projects/card3.jpeg')}/>
                            </div>
                        </div>

                        <div className="project-layout-1 lay2" ref={project}>
                            <div className="image-left">
                            <p className="text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                        <img src={require('../img/Projects/card4.jpg')}/>
                        </div>
                            <div className="image-right">
                                <img src={require('../img/Projects/card3.jpeg')}/>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </section>

        </motion.div>
     );
}
 
export default Project1;
