/*********************IMPORTS**********************/

import * as THREE from "three";

import imageOne from "../img/Projects/link curved images/1.jpeg";
import imageTwo from "../img/Projects/link curved images/2.jpeg";
import imageThree from "../img/Projects/link curved images/3.jpeg";
import imageFour from "../img/Projects/link curved images/4.jpeg";

import {Reveal} from "./oson";

/********************STARTUP**********************/

export default function startup() {
    const startupText = document.querySelectorAll(".startup__text");
    const startup = document.querySelector(".website__startup");

    for (let i = 0; i < startupText.length; i++) {
        startupText[i].innerHTML = startupText[i].innerText
            .split("")
            .map((char, i) => `<span style="--i:${i + 1};" class="startup__text-span">${char}</span>`)
            .join("");
    }

    setTimeout(() => {
        startup.style.display = "none";
        document.body.removeAttribute("linked");
    }, 3500);

    window.scrollTo(0, 0);

    const week = document.querySelector(".footer__week");
    const hour = document.querySelector(".footer__hour");
    const minute = document.querySelector(".footer__minute");
    const footerLink = document.querySelector(".footer__mainLink");

    const weekday = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

    new Reveal({
        element: footerLink,
        className: "active",
        distance: 10,
        repeat: true,
        timeout: .3,
    });

    function time() {
        let d = new Date();
        week.innerText = weekday[d.getDay()];
        hour.innerText = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
        minute.innerText = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();

        setTimeout(() => {
            time();
        }, 5000);
    }

    time();
}


function smooth() {
    const scrollable = document.querySelector(".scrollable");

    let current = 0;
    let target = 0;
    const ease = 0.06;

    function lerp(start, end, t) {
        return start * (1 - t) + end * t;
    }

    function init() {
        document.body.style.height = `${scrollable.getBoundingClientRect().height}px`;
    }

    function smoothScroll() {
        target = window.scrollY;
        current = lerp(current, target, ease);
        scrollable.style.transform = `translate3d(0, ${-current}px, 0)`;
        window.requestAnimationFrame(smoothScroll);
    }

    init();
    smoothScroll();
}

// smooth()

const curveImage = {
    elasticity: 7,
};

const images = {
    imageOne,
    imageTwo,
    imageThree,
    imageFour,
};

export function three() {
    function lerp(start, end, t) {
        return start * (1 - t) + end * t;
    }

    let targetX = 0;
    let targetY = 0;

    const textureOne = new THREE.TextureLoader().load(images.imageOne);
    const textureTwo = new THREE.TextureLoader().load(images.imageTwo);
    const textureThree = new THREE.TextureLoader().load(images.imageThree);
    const textureFour = new THREE.TextureLoader().load(images.imageFour);

    class WebGL {
        constructor() {
            this.container = document.querySelector(".curve-wrapper");
            setTimeout(() => {
                this.canvas = document.querySelectorAll("canvas");
                this.container.addEventListener('mouseenter', () => {
                    this.canvas.forEach(e => e.style.zIndex = 0);
                });
                this.container.addEventListener('mouseleave', () => {
                    this.canvas.forEach(e => e.style.zIndex = -2);
                });
            }, 1000);

            this.links = [...document.querySelectorAll(".curve-li")];
            this.scene = new THREE.Scene();
            this.perspective = 1000;
            this.sizes = new THREE.Vector2(0, 0);
            this.offset = new THREE.Vector2(0, 0); // Positions of mesh on screen. Will be updated below.
            this.uniforms = {
                uTexture: {value: textureOne},
                uAlpha: {value: 0.0},
                uOffset: {value: new THREE.Vector2(0.0, 0.0)},
            };
            this.links.forEach((link, idx) => {
                link.addEventListener("mouseenter", () => {
                    switch (idx) {
                        case 0:
                            this.uniforms.uTexture.value = textureOne;
                            break;
                        case 1:
                            this.uniforms.uTexture.value = textureTwo;
                            break;
                        case 2:
                            this.uniforms.uTexture.value = textureThree;
                            break;
                        case 3:
                            this.uniforms.uTexture.value = textureFour;
                            break;
                    }
                });
                link.addEventListener("mouseleave", () => {
                    this.uniforms.uAlpha.value = lerp(this.uniforms.uAlpha.value, 0.0, 0.1);
                });
            });
            this.addEventListeners(document.querySelector(".curve-list"));
            this.setUpCamera();
            this.onMouseMove();
            this.createMesh();
            this.render();
        }

        get viewport() {
            let width = window.innerWidth;
            let height = window.innerHeight;
            let aspectRatio = width / height;

            return {
                width,
                height,
                aspectRatio,
            };
        }

        addEventListeners(element) {
            element.addEventListener("mouseenter", () => {
                this.linkHovered = true;
            });
            element.addEventListener("mouseleave", () => {
                this.linkHovered = false;
            });
        }

        setUpCamera() {
            window.addEventListener("resize", this.onWindowResize.bind(this));

            let fov = (180 * (2 * Math.atan(this.viewport.height / 2 / this.perspective))) / Math.PI;
            this.camera = new THREE.PerspectiveCamera(fov, this.viewport.aspectRatio, 0.1, 1000);
            this.camera.position.set(0, 0, this.perspective);

            this.renderer = new THREE.WebGL1Renderer({antialias: true, alpha: true});
            this.renderer.setSize(this.viewport.width, this.viewport.height);
            this.renderer.setPixelRatio(window.devicePixelRatio);
            this.container.appendChild(this.renderer.domElement);
        }

        createMesh() {
            this.geometry = new THREE.PlaneGeometry(1, 1, 20, 20);
            this.material = new THREE.ShaderMaterial({
                uniforms: this.uniforms,
                vertexShader: `
                uniform sampler2D uTexture;
                uniform vec2 uOffset;
                varying vec2 vUv;
  
                float M_PI = 3.141529;
  
                vec3 deformationCurve(vec3 position, vec2 uv, vec2 offset){
                    position.x = position.x + (sin(uv.y * M_PI) * offset.x);
                    position.y = position.y + (sin(uv.x * M_PI) * offset.y);
                    return position;
                }
  
                void main(){
                    vUv = uv;
                    vec3 newPosition = deformationCurve(position, uv, uOffset);
                    gl_Position = projectionMatrix * modelViewMatrix * vec4(newPosition, 1.0);
                }
              `,
                fragmentShader: `
                uniform sampler2D uTexture;
                uniform float uAlpha;
                uniform vec2 uOffset;
                varying vec2 vUv;
  
                vec3 rgbShift(sampler2D textureimage, vec2 uv, vec2 offset ){
                    float r = texture2D(textureimage, uv + offset).r;
                    vec2 gb = texture2D(textureimage, uv).gb;
                    return vec3(r, gb);
                }
  
                void main(){
                    // vec3 color = texture2D(uTexture, vUv).rgb;
                    vec3 color = rgbShift(uTexture, vUv, uOffset);
                    gl_FragColor = vec4(color, uAlpha);
                }
              `,
                transparent: true,
                // wireframe: true,
                // side: THREE.DoubleSide
            });
            this.mesh = new THREE.Mesh(this.geometry, this.material);
            this.sizes.set(420, 570, 1);
            this.mesh.scale.set(this.sizes.x, this.sizes.y, 1);

            this.mesh.position.set(this.offset.x, this.offset.y, 0);

            this.scene.add(this.mesh);
        }
        onWindowResize() {
            this.camera.aspect = this.viewport.aspectRatio;
            this.camera.fov = (180 * (2 * Math.atan(this.viewport.height / 2 / this.perspective))) / Math.PI;
            this.renderer.setSize(this.viewport.width, this.viewport.height);
            this.camera.updateProjectionMatrix();
        }

        onMouseMove() {
            window.addEventListener("mousemove", (e) => {
                targetX = e.clientX;
                targetY = e.clientY;
            });
        }
        ////////////////////////
        ///////////////////////
        render() {
            this.offset.x = lerp(this.offset.x, targetX, 0.1);
            this.offset.y = lerp(this.offset.y, targetY, 0.1);
            this.uniforms.uOffset.value.set((targetX - this.offset.x) * (curveImage.elasticity / 10000), -(targetY - this.offset.y) * (curveImage.elasticity / 10000));
            this.mesh.scale.set(this.sizes.x, this.sizes.y);
            this.mesh.position.set(this.offset.x - window.innerWidth / 2, -this.offset.y + window.innerHeight / 2, 0);

            // set uAlpha when list is hovered / unhovered
            this.linkHovered ? (this.uniforms.uAlpha.value = lerp(this.uniforms.uAlpha.value, 2.0, 0.1)) : (this.uniforms.uAlpha.value = lerp(this.uniforms.uAlpha.value, 0.0, 0.1));

            for (let i = 0; i < this.links.length; i++) {
                if (this.linkHovered) {
                    this.links[i].style.opacity = 0.2;
                } else {
                    this.links[i].style.opacity = 1;
                }
            }

            this.renderer.render(this.scene, this.camera);
            window.requestAnimationFrame(this.render.bind(this));
        }
    }

    new WebGL();
}
