/*********************TEKSTLAR KETMA KET CHIQISHI************************/

export function OneByOne(elem, a, interval, timer) {
    for (let i = 0; i < elem.length; i++) {
        let timeout = elem[i].getAttribute("data-timeout");
        if (elem[i].hasAttribute("data-timeout")) {
            setTimeout(() => {
                elem[i].classList.add(a);
                elem[i].style.transitionDelay = `${timeout}`;
            }, timer * 1000);
        } else {
            for (let k = 0; k < elem.length; k++) {
                setTimeout(() => {
                    elem[k].classList.add(a);
                    elem[k].style.transitionDelay = `${interval * k}s`;
                }, timer * 1000);
            }
        }
    }
}

export function OneByOneScroll(elem, a, interval, distance, timer) {
    for (let i = 0; i < elem.length; i++) {
        let timeout = elem[i].getAttribute("data-timeout");
        if (elem[i].hasAttribute("data-timeout")) {
            setTimeout(() => {
                elem[i].classList.add(a);
                elem[i].style.transitionDelay = `${timeout}`;
            }, timer * 1000);
        } else {
            for (let k = 0; k < elem.length; k++) {
                setTimeout(() => {
                    elem[k].classList.add(a);
                    elem[k].style.transitionDelay = `${interval * k}s`;
                }, timer * 1000);
            }
        }
    }
}

export function OneByOneRemove(elem, a, interval, timer) {
    for (let i = 0; i < elem.length; i++) {
        let timeout = elem[i].getAttribute("data-timeout");
        if (elem[i].hasAttribute("data-timeout")) {
            setTimeout(() => {
                setTimeout(() => {
                    elem[i].classList.remove(a);
                }, timeout * 1000);
            }, timer * 1000);
        } else {
            for (let k = 0; k < elem.length; k++) {
                setTimeout(() => {
                    setTimeout(() => {
                        elem[k].classList.remove(a);
                    }, k * interval * 1000);
                }, timer * 1000);
            }
        }
    }
}

/*****************BARCHA ELEMENTLARGA CLASS BERISH FUNKSIYASI**********************/

export function classify(elem, c) {
    if (elem.length == undefined) {
        elem.classList.add(c);
    } else {
        elem.forEach((e) => {
            e.classList.add(c);
        });
    }
}

export function classifyRemove(elem, c) {
    if (elem.length == undefined) {
        elem.classList.remove(c);
    } else {
        elem.forEach((e) => {
            e.classList.remove(c);
        });
    }
}

export function classifyToggle(elem, c) {
    if (elem.length == undefined) {
        elem.classList.toggle(c);
    } else {
        elem.forEach((e) => {
            e.classList.toggle(c);
        });
    }
}

export function classifyOnly(elem, c, event) {
    elem.forEach((a) => {
        a.addEventListener(event, () => {
            elem.forEach((e) => {
                e.classList.remove(c);
            });
            a.classList.add(c);
        });
    });
}

/*********BARCHA ELEMENTLARGA ATTRIBUT BERISH FUNKSIYASI*********/

export function attribution(elem, c, a) {
    elem.forEach((e) => {
        e.setAttribute(c, a);
    });
}

export function attributionRemove(elem, c, a) {
    elem.forEach((e) => {
        e.removeAttribute(c, a);
    });
}

export function attributionOnly(elem, c, attr, event) {
    elem.forEach((a) => {
        a.addEventListener(event, () => {
            elem.forEach((e) => {
                e.removeAttribute(c, attr);
            });
            a.setAttribute(c, attr);
        });
    });
}

/*********SCROLL BOLGANDA ELEMENTLAR OZGARISHI*********/

export function reveal(elem, c, point, timer, line) {
    let vh = window.innerHeight;

    window.addEventListener("resize", () => {
        vh = window.innerHeight;
    });

    if (elem.length == undefined) {
        if (elem.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
            setTimeout(() => {
                elem.classList.add(c);
            }, timer * 1000);
        }
        window.addEventListener("scroll", () => {
            if (elem.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
                setTimeout(() => {
                    elem.classList.add(c);
                }, timer * 1000);
            }
        });

        if (line) {
            const name = document.createElement("span");
            document.body.appendChild(name);
            name.innerText = elem.className;
            name.style.color = "red";
            name.style.position = "fixed";
            name.style.bottom = `${point}vh`;
            name.style.right = `0`;
        }
    } else {
        elem.forEach(function (e) {
            if (e.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
                setTimeout(() => {
                    e.classList.add(c);
                }, timer * 1000);
            }
        });
        elem.forEach(function (e) {
            window.addEventListener("scroll", () => {
                if (e.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
                    setTimeout(() => {
                        e.classList.add(c);
                    }, timer * 1000);
                }
            });
        });

        if (line) {
            const name = document.createElement("span");
            document.body.appendChild(name);
            name.innerText = elem[0].className;
            name.style.color = "red";
            name.style.position = "fixed";
            name.style.bottom = `${point}vh`;
            name.style.right = `0`;
        }
    }

    if (line) {
        const para = document.createElement("span");
        document.body.appendChild(para);
        para.style.width = "100%";
        para.style.background = "red";
        para.style.height = "1px";
        para.style.position = "fixed";
        para.style.bottom = `${point}vh`;
    }
}

export class Reveal {
    constructor(config) {
        this.elem = config.element;
        const c = config.className;
        var point = config.distance;
        const timer = config.timeout;
        const reverse = config.repeat;
        var reverseP = config.distanceRepeat;
        this.line = config.bounder;

        var vh = window.innerHeight;

        window.addEventListener("resize", () => {
            vh = window.innerHeight;
        });

        if (this.elem.length == undefined) {
            if (this.elem.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
                setTimeout(() => {
                    this.elem.classList.add(c);
                }, timer * 1000);
            }
            window.addEventListener("scroll", () => {
                if (this.elem.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
                    setTimeout(() => {
                        this.elem.classList.add(c);
                    }, timer * 1000);
                }

                if (reverse == true) {
                    if (this.elem.getBoundingClientRect().top > (vh * (100 - reversePoint())) / 100) {
                        setTimeout(() => {
                            this.elem.classList.remove(c);
                        }, timer * 1000);
                    }
                }
            });

            if (this.line) {
                const name = document.createElement("span");
                document.body.appendChild(name);
                name.innerText = this.elem.className;
                name.style.color = "red";
                name.style.position = "fixed";
                name.style.bottom = `${point}vh`;
                name.style.right = `0`;
            }
        } else {
            this.elem.forEach(function (e) {
                if (e.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
                    setTimeout(() => {
                        e.classList.add(c);
                    }, timer * 1000);
                }
            });
            this.elem.forEach(function (e) {
                window.addEventListener("scroll", () => {
                    if (e.getBoundingClientRect().top < (vh * (100 - point)) / 100) {
                        setTimeout(() => {
                            e.classList.add(c);
                        }, timer * 1000);
                    }

                    if (reverse) {
                        if (e.getBoundingClientRect().top > (vh * (100 - reversePoint())) / 100) {
                            setTimeout(() => {
                                e.classList.remove(c);
                            }, timer * 1000);
                        }
                    }
                });
            });

            if (this.line) {
                const name = document.createElement("span");
                document.body.appendChild(name);
                name.innerText = this.elem[0].className;
                name.style.color = "red";
                name.style.position = "fixed";
                name.style.bottom = `${point}vh`;
                name.style.right = `0`;
            }
        }

        if (this.line) {
            const para = document.createElement("span");
            document.body.appendChild(para);
            para.style.width = "100%";
            para.style.background = "red";
            para.style.height = "1px";
            para.style.position = "fixed";
            para.style.bottom = `${point}vh`;
        }

        function reversePoint() {
            if (reverseP) {
                return reverseP;
            } else {
                return point;
            }
        }
    }
}

