import React from 'react';
import '../css/style.scss';
import { useEffect, useRef } from 'react';
import { three } from '../js/main';
import { Link } from 'react-router-dom';

const Curve = () => {
    if(window.innerWidth > 950){
        setTimeout(() => {
            three();
        }, 1000);
    };

    return (
        <div className='curve'>
            <section className='curve-wrapper'>
                <div className='cont'>
                    <ul className='curve-list'>
                        <li className='curve-li'><Link to='/bestofyear' className='curve-link'>Plants Design</Link></li>
                        <li className='curve-li'><Link to='/bestofyear' className='curve-link'>Minimalism</Link></li>
                        <li className='curve-li'><Link to='/bestofyear' className='curve-link'>Transitional</Link></li>
                        <li className='curve-li'><Link to='/bestofyear' className='curve-link'>Industrial</Link></li>
                    </ul>
                </div>
            </section>
        </div>
    )
}

export default Curve;

