import {Routes, Route, useLocation} from "react-router-dom";
import Home from "../Home";
import About from "../About";
import Projects from "../Projects";
import ProjectPage2 from "../project-pages/page2";
import Best from "../BestOfYear";
import Project1 from "../project-pages/project1";

import {AnimatePresence} from "framer-motion";

export default function RouteComponents() {
    const location = useLocation();

    return (
        <AnimatePresence>
            <Routes location={location} key={location.pathname}>
                <Route path="/" exact element={<Home />} />
                <Route path="/about" element={<About />} />
                <Route path="/projects/page/1" element={<Projects />} />
                <Route path="/projects/page/1/project1" element={<Project1 />} />
                <Route path="/projects/page/2" element={<ProjectPage2 />} />
                <Route path="/bestofyear" element={<Best />} />
            </Routes>
        </AnimatePresence>
    );
}
