import React, {useRef} from "react";
import {useEffect, useState} from "react";
import "./css/style.scss";
import Bottom from "./bottom";

const Footer = () => {
  return (
    
    <div className="content">
      <div className="footer">
        <div className="footer__content">
          <div className="footer__info">
            <p className="footer__location">Based in Tashkent, Uzbekistan</p>
            <div className="footer__time-wrapper">
              <span className="footer__time-info">Local Time: </span>
              <div className="footer__time">
                <span className="footer__week"></span>
                <span className="footer__hour"></span>
                <span className="dots">:</span>
                <span className="footer__minute"></span>
              </div>
            </div>
          </div>

          <div className="footer__main">

            <div className="footer__list-wrapper">
              <p className="footer__list-title">Sitemap</p>
              <ul className="footer__list">
                <li><a href="" className="footer__link">Home</a></li>
                <li><a href="" className="footer__link">About</a></li>
                <li><a href="" className="footer__link">Projects</a></li>
                <li><a href="" className="footer__link">Team</a></li>
                <li><a href="" className="footer__link">Contact</a></li>
              </ul>
            </div>

            <div className="footer__list-wrapper">
              <p className="footer__list-title">Social</p>
              <ul className="footer__list">
                <li><a href="https://www.instagram.com/" target="_blank" className="footer__link">Instagram</a></li>
                <li><a href="https://web.telegram.org/z/" target="_blank" className="footer__link">Telegram</a></li>
                <li><a href="https://www.youtube.com/" target="_blank" className="footer__link">Youtube</a></li>
                <li><a href="https://www.facebook.com/" target="_blank" className="footer__link">Facebook</a></li>
                <li><a href="https://twitter.com/" target="_blank" className="footer__link">Twitter</a></li>
              </ul>
            </div>

          </div>

          <div className="footer__bottom">
            <a href="" className="footer__mainLink">
              <span>Get </span>
              <span>in </span>
              <span>touch</span>
              <hr className="line"/></a>
          </div>

          <p className="footer__end-text">Friday Interior Design 2023</p>
        </div>
      </div>
    </div>
  );
};

export default Footer;
