import React from "react";
import {useEffect, useRef} from "react";
import App from './App';
import p1 from "./img/Projects/projects/p1.jpg";
import {OneByOne, Reveal} from "./js/oson";
import { motion } from "framer-motion";

const About = () => {

  const headerTitle = useRef();
  
  const blobs2 = useRef([]);
  const blobs = (el) => {
    if(el && !blobs2.current.includes(el)){
      blobs2.current.push(el);
    };
  };
  
  const s1Img = useRef();
  const s3Img = useRef();
  const partner = useRef([]);
  
  const partners = (el) => {
    if(el && !partner.current.includes(el)){
      partner.current.push(el);
    };
  };

  const s1Title = useRef();
  const s1Text = useRef();
  
  useEffect(() => {
    
    console.log(App);
    // window.onloadeddata = aboutAnimation();
    if (document.body.hasAttribute("linked")) {
      setTimeout(() => {
          headerTitle.current.classList.add("active");
          blobs2.current.forEach((e) => {
              e.style.opacity = 1;
          });
      }, 2900);
    } else {
      setTimeout(() => {
          headerTitle.current.classList.add("active");
          blobs2.current.forEach((e) => {
              e.style.opacity = 1;
          });
      }, 800);
    }

    new Reveal({
      element: s1Img.current,
      className: "active",
      distance: 90,
    });
  
    new Reveal({
        element: s3Img.current,
        className: "active",
        distance: 40,
    });

    window.addEventListener('scroll', () => {
      partner.current.forEach(e => {
        if(e.getBoundingClientRect().top < window.innerHeight){
          OneByOne(e, 'active', 0.2);
        };
      });
    });

    new Reveal({
      element: s1Title.current,
      className: 'active',
      distance: 20
    });

    new Reveal({
      element: s1Text.current,
      className: 'active',
      distance: 15,
    });
  }, []);

  return (
    <motion.div className="About"
      initial={{ opacity: 0, transition: {duration: 0} }}
      animate={{ opacity: 1,transition: {duration: 1} }}
      exit={{ opacity: 0, transition: {duration: 0} }}
    >
      <header>
        <div className="bg bg1" ref={blobs}></div>
        <div className="bg bg2" ref={blobs}></div>
        <div className="header__container">
          <div className="header__content">
            <div className="header__title" ref={headerTitle}>
              <div className="break">
                <span>Diversity</span>
              </div>
              <div className="break">
                <span>in </span>
                <span>experience</span>
              </div>
              <div className="break">
                <span>brings </span>
                <span>about</span>
              </div>
              <div className="break">
                <span>expertise</span>
              </div>
            </div>
          </div>
        </div>
      </header>

      <main>
        <section className="s1">
          <div className="s1__container">
            <div className="s1__content">
              <div className="s1__img" ref={s1Img}>
                <img src={p1} />
              </div>

              <div className="s1__info">
                <div className="s1__title" ref={s1Title}>What is Friday?</div>
                <div className="s1__text" ref={s1Text}>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
                  laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* -----------------Section 2------------------- */}
        <section className="s2">
          <div className="s2__container">
            <div className="s2__content">
              <div className="s2__title">Our Partners</div>
              {/* ------ */}
              <div className="partners">
                <div className="partner" ref={partners}>
                  <img src={require("./img/About/l1.png")} />
                </div>
                <div className="partner" ref={partners}>
                  <img src={require("./img/About/l2.png")} />
                </div>
                <div className="partner" ref={partners}>
                  <img src={require("./img/About/l3.png")} />
                </div>
                <div className="partner" ref={partners}>
                  <img src={require("./img/About/l4.png")} />
                </div>
                <div className="partner" ref={partners}>
                  <img src={require("./img/About/l5.png")} />
                </div>
                <div className="partner" ref={partners}>
                  <img src={require("./img/About/l6.png")} />
                </div>
              </div>
              {/* ------ */}
            </div>
          </div>
        </section>

        {/* -------------------------Section 3----------------------------- */}

        <section className="s3">
          <div className="s3__container">
            <div className="s3__content">
              <div className="s3__img" ref={s3Img}>
                <div className="s3__img-left">
                  <p className="s3__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
                  <img src={p1} />
                </div>
                <div className="s3__img-right">
                  <img src={p1} />
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
    </motion.div>
  );
};

export default About;
