import React from "react";
import './css/style.scss';
import { menuBtnClicked } from "./js/main";
import { Link } from "react-router-dom";
import { useState, useRef, useEffect } from "react";
import instaLogo from './img/icons8-instagram-100.svg';
import facebookLogo from './img/icons8-facebook.svg';
import twitterLogo from './img/icons8-twitter.svg';
import { transform } from "framer-motion";
import { classifyToggle } from "./js/oson";

const Nav = () => {

    const menuBtn = useRef();
    const menu = useRef();
    const menuWrapper = useRef();
    const openMenu = useRef();
    const closeMenu = useRef();
    const menuLine2 = useRef([]);
    const navLogo = useRef();
    const btnRound = [];
    const btnSpans = useRef([]);
    const btnSpans2 = (el) => {
        if(el && !btnSpans.current.includes(el)){
            btnSpans.current.push(el);
        };
    };
    
    const menuLine = (el) => {
        if(el && !menuLine2.current.includes(el)){
            menuLine2.current.push(el);
        };
    };

    const menuLink2 = useRef([]);
    const menuLink = (el) => {
        if(el && !menuLink2.current.includes(el)){
            menuLink2.current.push(el);
        };
    };

    for(let i = 0; i < 36; i++){
        btnRound.push(
            <span className="menu__textor" style={{transform: `rotate(${i * 10}deg)`}} key={i} ref={btnSpans2}></span>
        )
    }

    const activate = (el) => {
        if(menuBtn.current.classList.contains('active')){
            menuBtn.current.classList.remove('active');
            openMenu.current.classList.add('unactive');
            closeMenu.current.classList.remove('unactive');
            menu.current.style.transform = 'translateY(0)';
            openMenu.current.style.color = menuSettings.btnColor;
            navLogo.current.style.color = menuSettings.btnColor;
            btnSpans.current.forEach(e => {
                e.style.height = '0.50vh'
            });
        }else{
            menuBtn.current.classList.add('active');
            openMenu.current.classList.remove('unactive');
            closeMenu.current.classList.add('unactive');
            menu.current.style.transform = 'translateY(-100vh)';
            btnSpans.current.forEach(e => {
                e.style.height = '0';
            });
            setTimeout(() => {
                openMenu.current.style.color = menuSettings.navBtnColor;
                navLogo.current.style.color = menuSettings.navBtnColor;
            }, 500);
        };
    };

    useEffect(() => {
        openMenu.current.style.color = menuSettings.navBtnColor;
        closeMenu.current.style.color = menuSettings.btnColor;
        navLogo.current.style.color = menuSettings.navBtnColor;

        for(let i = 0; i < menuLink2.current.length - 1; i++){
            menuLink2.current[i].addEventListener('click', () => {
                menu.current.style.transform = 'translateY(-100vh)';
                closeMenu.current.classList.add('unactive');
                openMenu.current.classList.remove('unactive');
                menuBtn.current.classList.add('active');
                document.body.removeAttribute('linked');
                window.scrollTo(0, 0);
                btnSpans.current.forEach(e => {
                    e.style.height = '0';
                });
                setTimeout(() => {
                    openMenu.current.style.color = menuSettings.navBtnColor;
                    navLogo.current.style.color = menuSettings.navBtnColor;
                }, 500);
                btnSpans.current.forEach(e => {
                    e.style.height = '0';
                });
            });
        };

        navLogo.current.addEventListener('click', () => {
            menu.current.style.transform = 'translateY(-100vh)';
            closeMenu.current.classList.add('unactive');
            openMenu.current.classList.remove('unactive');
            menuBtn.current.classList.add('active');
            document.body.removeAttribute('linked');
            window.scrollTo(0, 0);
            btnSpans.current.forEach(e => {
                e.style.height = '0';
            });
            setTimeout(() => {
                openMenu.current.style.color = menuSettings.navBtnColor;
                navLogo.current.style.color = menuSettings.navBtnColor;
            }, 500);
            btnSpans.current.forEach(e => {
                e.style.height = '0';
            });
        });

        for (let i = 0; i < menuLine2.current.length; i++) {
            let arr = i % 2 == 0;
            let arr3 = i % 3 == 2;

            if (!arr) {
                menuLink2.current[i].style.paddingRight = "16vw";
                menuLink2.current[i].style.justifyContent = "end";
            } else if (arr3) {
                menuLink2.current[i].style.paddingLeft = "15vw";
                menuLink2.current[i].style.justifyContent = "start";
            } else {
                menuLink2.current[i].style.paddingLeft = "6vw";
                menuLink2.current[i].style.justifyContent = "start";
            };

            menuLink2.current[i].addEventListener("mouseenter", () => {
                menuLine2.current[i].style.opacity = "1";
                menuLine2.current[i].style.transition = ".5s ease";
                for (let j = 0; j < menuLine2.current.length; j++) {
                    menuLink2.current[j].style.opacity = ".3";
                };

                menuLink2.current[i].style.opacity = "1";
            });

            menuLink2.current[i].addEventListener("mouseleave", () => {
                menuLine2.current[i].style.opacity = "0";
                menuLine2.current[i].style.transition = ".5s ease";
                for (let j = 0; j < menuLine2.current.length; j++) {
                    menuLink2.current[j].style.opacity = "1";
                };
            });
        };
    }, []);

    const menuSettings = {
        btnColor: 'rgb(224, 224, 224)',
        navBtnColor: 'black',
    };

    return (
        <div className="menuApp" activated='true'>

            {/* ------------------------------------------------------------MENU--------------------------------------------------------------- */}

            <div className="menu" ref={menu}>
                <div className="menu__content" ref={menuWrapper}>

                    {/* -------------------MENU TOP ITEMS------------------- */}

                    <div className="menu__top">
                        <ul className="menu__icons">
                            <li><a><img className="menu__icon" src={instaLogo} style={{ marginTop: '.4vh' }}></img></a></li>
                            <li><a><img className="menu__icon" src={facebookLogo}></img></a></li>
                            <li><a><img className="menu__icon" src={twitterLogo} style={{ marginTop: '.4vh' }}></img></a></li>
                        </ul>
                        <div className="shape">
                            <div className="circle circle1"></div>
                            <div className="circle circle2"></div>
                            <div className="circle circle3"></div>
                            <div className="center"></div>
                        </div>
                    </div>

                    {/* ---------------------MENU LINKS--------------------- */}
                    <ul className="menu__list">

                        <li>
                            <Link to='/' className="menu__link" ref={menuLink}><span className="menu__link-number">i</span>Home</Link>
                            <span className="menu__line-wrapper"><hr className="menu__line" ref={menuLine}/></span>
                        </li>
                        <li>
                            <Link to='/projects/page/1' className="menu__link" ref={menuLink}><span className="menu__link-number">ii</span>Projects</Link>
                            <span className="menu__line-wrapper"><hr className="menu__line" ref={menuLine}/></span>
                        </li>
                        <li>
                            <Link to='/about' className="menu__link" ref={menuLink}><span className="menu__link-number">iii</span>About</Link>
                            <span className="menu__line-wrapper"><hr className="menu__line" ref={menuLine}/></span>
                        </li>
                        <li>
                            <Link to='/bestofyear' className="menu__link" ref={menuLink}><span className="menu__link-number">iv</span>Best of year</Link>
                            <span className="menu__line-wrapper"><hr className="menu__line" ref={menuLine}/></span>
                        </li>
                        <li>
                            <a className="menu__link" ref={menuLink}><span className="menu__link-number">v</span>Contact</a>
                            <span className="menu__line-wrapper"><hr className="menu__line" ref={menuLine}/></span>
                        </li>

                    </ul>
                </div>
            </div>

            {/* ---------------------------------------------------------NAVIGATION------------------------------------------------------------ */}

            <nav className="nav">
                <div className="nav__content">
                    <Link to='/' className="nav__logo" ref={navLogo}>FRIDAY</Link>

                    <div className="menu__btn active" ref={menuBtn} onClick={activate}>
                        <div className="menu__close-text">
                            <button className="menu__close-btn" ref={openMenu}>Menu</button>
                            <button className="menu__close-btn2 unactive" ref={closeMenu}>Close</button>
                        </div>
                        <div className="menu__btn-round">
                            {btnRound}
                        </div>
                    </div>
                </div>
            </nav>

        </div>
    );
};


export default Nav;



