import React from "react";
import {Link} from "react-router-dom";
import {useRef, useEffect, useState} from "react";
import {classify, classifyRemove, reveal, OneByOne} from "../js/oson";
import {motion} from "framer-motion";
import Curve from "../curve/curve";

import "../css/style.scss";
// import { projectsAnimation } from "../js/main";
import {Records} from "../project";

import p1 from "../img/Projects/projects/p1.jpg";
import p2 from "../img/Projects/projects/p2.jpg";
import p3 from "../img/Projects/projects/p3.jpg";
import p4 from "../img/Projects/projects/p4.jpg";
import p5 from "../img/Projects/projects/p5.jpg";
import p13 from "../img/Projects/projects/p13.jpg";
import p7 from "../img/Projects/projects/p7.jpg";
import p8 from "../img/Projects/projects/p8.jpg";

// import slide1 from './img/Projects/slide1.jpg';

const Projects2 = () => {
    const [width, setWidth] = useState(0);
    const background = useRef();
    const s1 = useRef();

    const imgw = useRef([]);
    const imageWrapper = (el) => {
        if (el && !imgw.current.includes(el)) {
            imgw.current.push(el);
        }
    };

    const spanTs = useRef([]);
    const spanTitles = (el) => {
        if (el && !spanTs.current.includes(el)) {
            spanTs.current.push(el);
        }
    };

    const s1TopSp = useRef([]);
    const s1TopSpan = (el) => {
        if (el && !s1TopSp.current.includes(el)) {
            s1TopSp.current.push(el);
        }
    };

    const s1Title = useRef();
    const s1TopTitle = useRef();

    function deviceAnalysis(n) {
        switch (n) {
            case 1:
                if (window.innerWidth > 950) {
                    return "mousedown";
                } else {
                    return "touchstart";
                }
                break;

            case 2:
                if (window.innerWidth > 950) {
                    return "mouseup";
                } else {
                    return "touchend";
                }
                break;
        }
    }

    useEffect(() => {
        setTimeout(() => {
            window.scrollTo(0, 0);
        }, 500);

        setWidth(background.current.scrollWidth / 2 / 1.59);

        background.current.addEventListener(deviceAnalysis(1), () => {
            classify(imgw.current, "active");
            OneByOne(spanTs.current, "animate", 0.15);
            OneByOne(s1TopSp.current, "animate", 0.15, 1);

            setTimeout(() => {
                s1Title.current.style.display = "none";
            }, 1500);
        });

        background.current.addEventListener(deviceAnalysis(2), () => {
            classifyRemove(imgw.current, "active");
        });
        background.current.addEventListener("mouseleave", () => {
            classifyRemove(imgw.current, "active");
        });
    }, []);

    /*************************************** */

    const projectImg2 = useRef([]);
    const projectImg = (el) => {
        if (el && !projectImg2.current.includes(el)) {
            projectImg2.current.push(el);
        }
    };

    const prLine2 = useRef([]);
    const prLine = (el) => {
        if (el && !prLine2.current.includes(el)) {
            prLine2.current.push(el);
        }
    };

    const projectTitle2 = useRef([]);
    const projectTitle = (el) => {
        if (el && !projectTitle2.current.includes(el)) {
            projectTitle2.current.push(el);
        }
    };

    const projectText2 = useRef([]);
    const projectText = (el) => {
        if (el && !projectText2.current.includes(el)) {
            projectText2.current.push(el);
        }
    };

    const centerLine2 = useRef([]);
    const centerLine = (el) => {
        if (el && !centerLine2.current.includes(el)) {
            centerLine2.current.push(el);
        }
    };

    const projectsContent = useRef();

    const spanLink2 = useRef([]);
    const spanLink = (el) => {
        if (el && !spanLink2.current.includes(el)) {
            spanLink2.current.push(el);
        }
    };

    const lnkd = () => {
        document.body.removeAttribute("linked");
    };

    useEffect(() => {
        reveal(prLine2.current, "animate", 10);
        reveal(projectImg2.current, "animate", 10);
        reveal(projectTitle2.current, "animate", 0);
        reveal(projectText2.current, "animate", 0, 0.3);
        reveal(centerLine2.current, "animate", 0, 0.3);
        reveal(projectsContent.current, "animate", 0, 0.3);

        for (let i = 0; i < spanLink2.current.length; i++) {
            spanLink2.current[i].innerHTML = `
            ${i + 1}
                <svg>
                    <circle cx="50%" cy="50%" r="50%" fill="none" className="header__circle"></circle>
                </svg>
            `;
        }
    }, []);

    return (
        <motion.div className="Projects-page scroll-smoother" activated="true"
            initial={{ opacity: 0, transition: {duration: 0} }}
            animate={{ opacity: 1,transition: {duration: 1} }}
            exit={{ opacity: 0, transition: {duration: 0} }}
        >
            {/* -----------------------------------------------------------------MAIN CONTENT------------------------------------------------------------------------ */}

            <section className="s1" ref={s1}>
                <div className="s1__container">
                    <div className="s1__content">
                        <div className="s1__top-title" ref={s1TopTitle}>
                            <span className="title" ref={s1TopSpan}>
                                Cultural
                            </span>
                            <span className="title" ref={s1TopSpan}>
                                art and
                            </span>
                            <span className="title" ref={s1TopSpan}>
                                environments
                            </span>
                        </div>

                        <div className="s1__title" ref={s1Title}>
                            <span className="title" ref={spanTitles}>
                                Cultural
                            </span>
                            <br />
                            <span className="title" ref={spanTitles}>
                                art and
                            </span>
                            <br />
                            <span className="title" ref={spanTitles}>
                                environments
                            </span>
                        </div>

                        <motion.div ref={background} drag="x" dragConstraints={{right: width, left: -width}} className="s1__bg-wrapper">
                            <div className="s1__background">
                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p1} />
                                        <span></span>
                                    </div>
                                </div>

                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p2} />
                                        <span></span>
                                    </div>
                                </div>

                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p3} />
                                        <span></span>
                                    </div>
                                </div>

                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p4} />
                                        <span></span>
                                    </div>
                                </div>

                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p5} />
                                        <span></span>
                                    </div>
                                </div>

                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p8} />
                                        <span></span>
                                    </div>
                                </div>

                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p7} />
                                        <span></span>
                                    </div>
                                </div>

                                <div className="image__container">
                                    <div className="image__wrapper" ref={imageWrapper}>
                                        <img src={p13} />
                                        <span></span>
                                    </div>
                                </div>
                            </div>
                        </motion.div>
                    </div>
                </div>
            </section>

            <div className="s1__box">
                <div className="s1__box-content">
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                </div>
                <div className="s1__box-content">
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                    <span>FRIDAY</span>
                    <span>VENERD</span>
                    <span></span>
                    <span>VIERNES</span>
                </div>
            </div>

            <Curve />

            <section className="projects">
                <div className="projects__container">
                    <div className="projects__content" ref={projectsContent}>
                        {/* ------------------------------PROJECTS------------------------------ */}

                        {Records.map((record) => {
                            return (
                                <div className="projects__wrapper" key={record.id}>
                                    <span className="line" ref={prLine}></span>
                                    <span className="center-line" ref={centerLine}></span>

                                    <div className="project">
                                        <div className="project__img" ref={projectImg}>
                                            <Link onClick={lnkd} to="/projects/page/1/project1" style={{position: "absolute", height: "100%", width: "100%", top: "0", left: "0", zIndex: 3}}>
                                                <img src={record.image} />
                                            </Link>
                                        </div>
                                        <p className="project__title" ref={projectTitle}>
                                            {record.title}
                                        </p>
                                        <p className="project__text" ref={projectText}>
                                            {record.text}
                                        </p>
                                    </div>

                                    <div className="project">
                                        <div className="project__img img2" ref={projectImg}>
                                            <Link onClick={lnkd} to="/projects/page/1/project1" style={{position: "absolute", height: "100%", width: "100%", top: "0", left: "0", zIndex: 3}}>
                                                <img src={record.image2} />
                                            </Link>
                                        </div>
                                        <p className="project__title" ref={projectTitle}>
                                            {record.title2}
                                        </p>
                                        <p className="project__text" ref={projectText}>
                                            {record.text2}
                                        </p>
                                    </div>
                                </div>
                            );
                        })}

                        {/* ------------------------------PROJECTS END------------------------------ */}

                        {/* -------------PROJECTS PAGE TRANSISTORS---------------- */}
                    </div>

                    <div className="project__pages-wrapper">
                        <ul className="project__pages">
                            <li>
                                <span className="active" ref={spanLink}></span>
                            </li>
                            <li>
                                <Link to="/projects/page/2">
                                    <span ref={spanLink}></span>
                                </Link>
                            </li>
                            <li>
                                <span ref={spanLink}></span>
                            </li>
                            <li>
                                <span ref={spanLink}></span>
                            </li>
                            <li>
                                <span ref={spanLink}></span>
                            </li>
                            <li>
                                <span ref={spanLink}></span>
                            </li>
                            <li>
                                <span ref={spanLink}></span>
                            </li>
                        </ul>
                    </div>
                </div>
            </section>
        </motion.div>
    );
};

export default Projects2;
