import React, { useEffect } from "react";
import "./css/style.scss";
import { useRef } from "react";
import { Reveal } from "./js/oson";
import { motion } from "framer-motion";

const Best = () => {

  const headerTitle = useRef();
  const blob2 = useRef([]);
  const blob = (el) => {
    if(el && !blob2.current.includes(el)){
      blob2.current.push(el);
    };
  };
  const s1Img1 = useRef([]);
  const s1Img = (el) => {
    if(el && !s1Img1.current.includes(el)){
      s1Img1.current.push(el);
    };
  }

  const s1Img22 = useRef([]);
  const s1Img2 = (el) => {
    if(el && !s1Img22.current.includes(el)){
      s1Img22.current.push(el);
    };
  }

  const s1ContentTitle2 = useRef([]);
  const s1ContentTitle = (el) => {
    if(el && !s1ContentTitle2.current.includes(el)){
      s1ContentTitle2.current.push(el);
    };
  }
  

  useEffect(() => {
    
    setTimeout(() => {
      window.scrollTo(0, 0);
    }, 500);

    if (document.body.hasAttribute("linked")) {
      setTimeout(() => {
        headerTitle.current.classList.add("active");
        blob2.current.forEach((e) => {
              e.classList.add("active");
          });
      }, 2900);
    } else {
        setTimeout(() => {
          headerTitle.current.classList.add("active");
          blob2.current.forEach((e) => {
                e.classList.add("active");
            });
        }, 800);
    }

    new Reveal({
      element: s1Img1.current,
      className: "active",
      distance: 20,
    });

    new Reveal({
        element: s1Img22.current,
        className: "active",
        distance: 20,
    });

    new Reveal({
      element: s1ContentTitle2.current,
      className: 'active',
      distance: 20
    });
  }, []);

  return (
    <motion.div className="Best"
      initial={{ opacity: 0, transition: {duration: 0} }}
      animate={{ opacity: 1,transition: {duration: 1} }}
      exit={{ opacity: 0, transition: {duration: 0} }}
    >

      <header>
        <div className="bg bg1" ref={blob}></div>
        <div className="bg bg2" ref={blob}></div>
        <div className="bg bg3" ref={blob}></div>

        <div className="header__container">
          <div className="header__content">
            <div className="header__title" ref={headerTitle}>
              <div className="break">
                <span>Designs </span>
              </div>
              <div className="break">
                <span>that </span>
                <span>make </span>
              </div>
              <div className="break">
                <span>your </span>
                <span>eyes </span>
              </div>
              <div className="break">
                <span>sparkle</span>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* ---------------------------------------------- */}

      <section className="s1">
        <div className="s1__container">
          <div className="s1__content">
            {/* -------------------------------- */}
            <h2 className="s1__content-title" ref={s1ContentTitle}>Plants design</h2>
            <div className="s1__img" ref={s1Img}>
              <div className="s1__img-left">
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
                <img src={require("./img/Best/b1.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b2.jpg")} />
              </div>
            </div>
            <div className="s1__img2" ref={s1Img2}>
              <div className="s1__img-left">
                <img src={require("./img/Best/b3.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b4.jpg")} />
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
              </div>
            </div>

            {/* ------------------------------- */}

            <h2 className="s1__content-title" ref={s1ContentTitle}>Minimalism</h2>
            <div className="s1__img" ref={s1Img}>
              <div className="s1__img-left">
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
                <img src={require("./img/Best/b5.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b6.jpg")} />
              </div>
            </div>
            <div className="s1__img2" ref={s1Img2}>
              <div className="s1__img-left">
                <img src={require("./img/Best/b7.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b8.jpg")} />
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
              </div>
            </div>

            {/* ------------------------------- */}

            <h2 className="s1__content-title" ref={s1ContentTitle}>Transitional</h2>
            <div className="s1__img" ref={s1Img}>
              <div className="s1__img-left">
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
                <img src={require("./img/Best/b10.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b9.jpg")} />
              </div>
            </div>
            <div className="s1__img2" ref={s1Img2}>
              <div className="s1__img-left">
                <img src={require("./img/Best/b11.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b12.jpg")} />
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
              </div>
            </div>

            {/* --------------------------------- */}

            <h2 className="s1__content-title" ref={s1ContentTitle}>Industrial</h2>
            <div className="s1__img" ref={s1Img}>
              <div className="s1__img-left">
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
                <img src={require("./img/Best/b13.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b14.jpg")} />
              </div>
            </div>
            <div className="s1__img2" ref={s1Img2}>
              <div className="s1__img-left">
                <img src={require("./img/Best/b15.jpg")} />
              </div>
              <div className="s1__img-right">
                <img src={require("./img/Best/b16.jpg")} />
                <p className="s1__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,</p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </motion.div>
  );
};

export default Best;
